.head-box-layout.double-box {
  display: -ms-flexbox;
  display: flex; }
.head-box-layout .head-box-cover {
  padding-right: 30px; }
.head-box-layout .head-box-desc {
  padding-top: 150px; }
.head-box-layout .head-box-desc .title-h1 {
